.notesArea {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;

}

.note {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 309px;
  height: 240px;
  border: solid 5px;
  border-radius: 12px;
  color: #FFFF;
  border-color: #717171;
  margin-bottom: 1%;
  margin-right: 1%;
  margin-top: 1%;
  background-color: black;
}

.titleNote {
  display: flex;
  margin-top: 2%;
  width: 290px;
  height: 26px;
  border: solid 3px;
  border-radius: 8px;
  border-color: #717171;
  background-color: #2a1b2f;
  color: #efefef;
  font-family: Roboto Condensed;
  font-size: 20px;
}

.textNote {
  display: flex;
  margin-top: 2%;
  text-align: justify;
  width: 290px;
  height: 140px;
  border: solid 3px;
  border-radius: 8px;
  border-color: #717171;
  background-color: #2a1b2f;
  color: #efefef;
  font-family: Roboto Condensed;
  font-size: 20px;
}

.areaButtons {
  display: flex;
  width: 100%;
  margin-top: 13px;
  margin-bottom: 2px;
}

.buttonDelete {
  background-color: black;
  border: solid 2px;
  border-color: #FFFF;
  border-radius: 10px;
  width: -4px;
  width: 40px;
  height: 40px;
  margin-right: 168px;
  margin-left: 7px;
  padding: 0px;
  transition-duration: 0.4s;
}

.buttonDelete:hover {
  border-color: #962b7b;
}

.deleteImg {
  width: 32px;
  height: 30px;
}

.buttonViewNote {
  background-color: black;
  border: solid 2px;
  border-color: #FFFF;
  border-radius: 10px;
  width: -4px;
  width: 40px;
  height: 40px;
  margin-right: 15px;
  margin-left: 7px;
  padding: 0px;
  transition-duration: 0.4s;
}

.buttonViewNote:hover {
  border-color: #962b7b;
}

.ViewNoteImg {
  width: 32px;
  height: 30px;
}

.addBtnContainer{
  display: flex;
  justify-content: center;
}
.addNoteLabel {
  width: 225px;
  height: 36px;
  background-color: black;
  color: #FFFF;
  font-family: roboto;
  font-size: 30px;
  text-align: center;
}

.buttonAddNote {
  background-color: black;
  border: solid 2px;
  border-color: #FFFF;
  border-radius: 10px;
  width: -4px;
  width: 40px;
  height: 40px;
  padding: 0px;
  transition-duration: 0.4s;
}

.buttonAddNote:hover {
  border-color: #962b7b;
}

.imgAddNote {
  width: 32px;
  height: 30px;
}